export function is_device() {
  return !!(device);
}

export function is_connected() {
  return !!(connection)
}

export async function pair_device() {
  await Manager.requestDevice({
    filters: [
      {
        ...ADB_DEFAULT_DEVICE_FILTER,
        vendorId: 0x19D2,
        productId: 0x0452
      }
    ]
  });
  await autoconnect();
}

export async function list_existing() {
  devices = await Manager.getDevices(
    [
      {
        ...ADB_DEFAULT_DEVICE_FILTER,
        vendorId: 0x19D2,
        productId: 0x0452
      }
    ]
  );
  return devices;
}

export function select_existing(serial) {
  device = devices.filter(dev => { return dev.serial == serial; });
  console.log(device);
  if (device.length < 1) {
    device = null;
    return;
  }
  device = device[0];
  if (get_autoconnect()) {
    connect_device();
  }
}

export async function disconnect() {
  if (!device) {
    log("No device connected!")
    return;
  }
  try {
    await adb.close();
  } catch (e) {
    log("Error when closing adb");
    console.error(e);
  }
  log("ADB session closed");
  adb = null;
  try {
    await device.raw.close();
  } catch (e) {
    log("Error when closing connection");
    console.error(e);
  }
  log("Connection closed");
  connection = null;
  device = null;
  if (dev_list) {
    await draw_device_list();
  }
  if (onmonitor) {
    onmonitor();
  }
}

export async function autoconnect() {
  await list_existing();
  if (device && devices.filter(dev => { return dev.serial == device.serial }).length < 1) {
    device = null;
  }
  console.log(devices);
  // if (devices.length == 0) return [];
  if (!device && get_autoconnect() && devices.length > 0) {
    device = devices[0]
    await connect_device();
    if (onmonitor) {
      onmonitor();
    }
  }
  if (dev_list) {
    await draw_device_list();
  }
  return devices;
}

export function set_autoconnect(val) {
  val = "" + val;
  autoconnect_enabled = val;
  localStorage["autoconnect_enabled"] = val;
}

export function get_autoconnect() {
  return autoconnect_enabled == "true";
}

export function monitor_usb(dev_list_p, onmonitor_p) {
  dev_list = dev_list_p;
  onmonitor = onmonitor_p;
  navigator.usb.addEventListener("connect", () => {
    log("Connect event received, updating devices...");
    autoconnect();
  });
  navigator.usb.addEventListener("disconnect", () => {
    log("Disconnect event received, updating devices...");
    autoconnect();
  });
  log("Checking devices due to start...");
  autoconnect();
}

export async function draw_device_list() {
  const list_elem = dev_list;
  if (!devices) {
    await list_existing();
  }
  // log(devices.toString());
  list_elem.innerHTML = `<option value="null">No device</option>`;
  for (const dev of devices) {
    const opt = document.createElement("option");
    opt.textContent = `${dev.raw.productName}\t${dev.raw.manufacturerName}\t${dev.serial}`;
    opt.value = dev.serial;
    list_elem.appendChild(opt);
  }
  if (device) {
    list_elem.value = device.serial;
  } else {
    list_elem.value = "null";
  }
}

export async function connect_device() {
  console.log("connect dev")
  if (!device) {
    console.log("Device is null");
    alert("Device is null");
    return;
  }
  connection = await device.connect();
  log("Device connection opened...")
  const transport = await AdbDaemonTransport.authenticate({
    serial: device.serial,
    connection,
    credentialStore: CredentialStore,
  });
  log("ADB transport opened...")
  adb = new Adb(transport);
  log("ADB connection created!");
  // console.log(adb);
  log("Android version: " + await adb.getProp("ro.build.version.release"));
  if (onmonitor) {
    onmonitor();
  }
}

