export async function install_app(file, user) {
  if (!user) {
    user = "current";
  }
  //const file_input = document.querySelector("#apk");
  //const file = file_input.files[0];
  log("Acquiring sync...");
  const sync = await adb.sync();
  log("Sending APK...");
  await sync.write({ filename: "/data/local/tmp/temp.apk", file: file.stream() });
  sync.dispose();
  log("APK sent!");
  let res = null;
  log("Installing...");
  res = await godo("pm install-create --user " + user);
  const session = res.stdout.split("[")[1].split("]")[0];
  await godo(`pm install-write ${session} tomato /data/local/tmp/temp.apk`);
  await godo(`pm install-commit ${session}`);
  await adb.rm("/data/local/tmp/temp.apk");
}

export async function send_command_and_get_response(command, ignore_errors, silence_logs) {
  const process = await adb.subprocess.spawnAndWait(command, {
    protocols: [AdbSubprocessShellProtocol]
  });
  if (process.stderr) {
    if (!silence_logs) {
      log(process.stderr);
    }
    if (!ignore_errors) {
      throw process.stderr;
    }
  }
  if (!silence_logs) {
    log(process.stdout);
  }
  return process;
}

export async function send_to_device(file) {
  log("Acquiring sync...");
  const sync = await adb.sync();
  log("Sending...");
  await sync.write({ filename: "/sdcard/Download/" + file.name, file: file.stream() });
  await sync.dispose();
  log("Sent!");
}

export const godo = send_command_and_get_response;

export function log(msg) {
  if (konsole) {
    konsole.textContent += `[${(new Date()).toLocaleString()}] ${msg}\n`
    konsole.scrollTo(0, konsole.scrollHeight);
  } else {
    console.log(msg);
  }
  return msg;
}